import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ViewController } from 'ionic-angular';
import { ImageViewerController } from 'ionic-img-viewer';
import { ScreenOrientation } from '@ionic-native/screen-orientation';
/**
 * Generated class for the VuelosPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-vuelos',
  templateUrl: 'vuelos.html',
})
export class VuelosPage {
 public backgroundImage = 'assets/img/vale03.jpg';   
 testimonials = [
    {
      imageUrl: 'assets/img/17.jpg'
    },
    {
      imageUrl: 'assets/img/6.jpg'
    },
    {
      imageUrl: 'assets/img/7.jpg'
    },
    {
      imageUrl: 'assets/img/8.jpg'
    },
    {
      imageUrl: 'assets/img/9.jpg'
    },
  ];



  _imageViewerCtrl: ImageViewerController;

  public items: any = [];
  public error: string;
  public images: any = [];

constructor(public navCtrl: NavController, public navParams: NavParams,public viewCtrl: ViewController,imageViewerCtrl: ImageViewerController,public screenOrientation: ScreenOrientation) {
    this._imageViewerCtrl = imageViewerCtrl;
    //this.screenOrientation.lock(this.screenOrientation.ORIENTATIONS.PORTRAIT);

  }



  ionViewDidLoad() {
    this.viewCtrl.setBackButtonText('');
  }

  presentImage(myImage) {
    const imageViewer = this._imageViewerCtrl.create(myImage);
    imageViewer.present();
    this.viewCtrl.setBackButtonText('');
  }

   buscaridentificador(urlimagenes: string) {
    this.navCtrl.push('SliderWithArrowsPage', urlimagenes);
  } 


}



